import { getlivelist } from '@/api/live'
import Panl from '@/components/Panl'
import { PoweroffOutlined, QuestionCircleOutlined } from '@ant-design/icons'
import { Button, Card, Input, Popconfirm, Segmented, Space, Tabs, TabsProps, message } from 'antd'
import flvjs from 'flv.js'
import Hls from 'hls.js'
import { useEffect, useRef, useState } from 'react'
import ReactPlayer from 'react-player'
import './index.less'

window.Hls = Hls
const Live = () => {
  const refPlay = useRef(null)
  const [messageApi, contextHolder] = message.useMessage()
  const [reload, setReload] = useState(0) // 
  const [title, settitle] = useState('')
  const [inputValue, setInputValue] = useState('')

  const [url, seturl] = useState('http://127.0.0.1:8887/live/mylive2/index.m3u8')
  const myPlay = () => {
    seturl(inputValue)
    upload()
  }
  const items: TabsProps['items'] = [
    {
      key: '1',
      label: '',
      children: 'Content of Tab Pane 1'
    },
    {
      key: '2',
      label: '',
      children: 'Content of Tab Pane 2'
    },
    {
      key: '3',
      label: '',
      children: (
        <Space.Compact style={{ width: '100%' }}>
          <Input
            placeholder=""
            defaultValue=""
            onChange={(e) => setInputValue(e.target.value)}
            value={inputValue}
          />
          <Button type="primary" onClick={myPlay}>
            
          </Button>
        </Space.Compact>
      )
    }
  ]

  const startplay = () => {
    if (flvjs.isSupported()) {
      const videoElement = document.getElementById('myvideo')
      const flvPlayer = flvjs.createPlayer({
        type: 'flv',
        url: 'http://127.0.0.1:8887/live/mylive.flv'
      })
      flvPlayer.attachMediaElement(videoElement)
      flvPlayer.load()
      flvPlayer.play()
    }
  }

  // 
  const upload = () => {
    setReload(reload + 1) // 
  }
  // 
  const getlist = async () => {
    const { data } = await getlivelist()
  }

  const tabsChange = (activeKey: string) => {
    const label = (items.find((item) => item.key === activeKey)?.label as string) || ''

    settitle(label)
  }

  const playError = () => {
    messageApi.open({
      type: 'error',
      content: ',!'
    })
  }

  useEffect(() => {
    getlist()
  }, [])

  return (
    <Panl title="" source={''}>
      {contextHolder}
      <div className="live-box">
        <Card title="" bordered={false} style={{ width: 300 }}>
          <p>Card content</p>
        </Card>
        <div className="live-content">
          <Card title={title} bordered={false}>
            <div className="live-video">
              <ReactPlayer
                ref={refPlay}
                key={reload} // key
                className="live-player"
                onError={playError}
                url={url}
                config={{
                  file: {
                    attributes: {
                      poster:
                        'https://gitee.com/jiuxiangyangguang/myimg/raw/master/tree/master/c759ef18204881fd870e0450ddf50b0.jpg1695697934393' // URLURL
                    },
                    hlsOptions: { autoStartLoad: true }, // HLS.js
                    hlsVersion: '1.4.12', // HLS.js
                    forceHLS: true // HLS
                  }
                }}
                controls
                playing
              />
            </div>
          </Card>
          <Card title="" bordered={false}>
            <Tabs defaultActiveKey="1" onChange={tabsChange} items={items} />
          </Card>
        </div>

        <div className="live-info">
          <Card title="" bordered={false} style={{ width: 300 }}>
            <p>Card content</p>
          </Card>
          <Card title="" bordered={false} style={{ width: 300 }}>
            <Segmented options={['', '']} />
            <Button block type="dashed" onClick={upload} style={{ marginTop: '10px' }}>
              
            </Button>
            <Button block type="dashed" style={{ marginTop: '10px' }}>
              
            </Button>
            <Popconfirm
              title=""
              description="?"
              icon={<QuestionCircleOutlined style={{ color: 'red' }} />}>
              <Button
                style={{ marginTop: '10px' }}
                block
                type="dashed"
                danger
                icon={<PoweroffOutlined />}>
                
              </Button>
            </Popconfirm>
          </Card>
        </div>
      </div>
    </Panl>
  )
}

export default Live
